.dropdown {
  display: relative; }

.dropdown__toggle-btn {
  height: 34px;
  border-radius: 1px;
  background-color: rgba(255, 255, 255, 0.83);
  border: solid 1px #bdc3c7; }

.dropdown__toggle-btn .fa {
  position: absolute;
  right: 10px;
  top: 10px; }

.dropdown button {
  display: block;
  width: 100%;
  text-align: left;
  background: #fff; }

.dropdown button:hover {
  background: #2980B9;
  color: #fff; }

.dropdown.is-open .dropdown__options {
  display: block; }

.dropdown__options {
  display: none;
  position: absolute;
  top: 34px;
  left: 0;
  z-index: 9999;
  right: 0;
  border-radius: 1px;
  background-color: rgba(255, 255, 255, 0.9);
  border: solid 1px #bdc3c7;
  max-height: 260px;
  overflow: auto; }

.dropdown__options li {
  display: block; }



