.progress-bar {
  width: 100%;
  background: #f5f5f5;
  -webkit-box-shadow: none;
          box-shadow: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }

.progress {
  background: -webkit-gradient(linear, left top, right top, from(#c0392b), color-stop(#f1c40f), color-stop(#c0392b), to(#f1c40f));
  background: -webkit-linear-gradient(left, #c0392b, #f1c40f, #c0392b, #f1c40f);
  background: -o-linear-gradient(left, #c0392b, #f1c40f, #c0392b, #f1c40f);
  background: linear-gradient(to right, #c0392b, #f1c40f, #c0392b, #f1c40f);
  border-radius: 1px;
  -webkit-animation: gradient 3s linear infinite;
          animation: gradient 3s linear infinite;
  background-position: 0% 0%;
  background-size: 500% auto;
  height: 10px; }

@-webkit-keyframes gradient {
  0% {
    background-position: 0 0; }
  100% {
    background-position: 100% 0; } }

@keyframes gradient {
  0% {
    background-position: 0 0; }
  100% {
    background-position: 100% 0; } }



