html {
  font-size: 16px; }

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  color: #333333;
  font-size: 1rem; }

h1 {
  margin-top: 0;
  font-size: 1.4rem;
  font-weight: 800; }

h2 {
  font-size: 1rem;
  font-weight: 800; }

h3 {
  font-size: 1rem;
  font-weight: 800; }

h4 {
  margin-top: 0;
  font-weight: 800; }

.navbar-header {
  background: #fff; }

.form-control {
  border-radius: 2px;
  -webkit-box-shadow: none;
          box-shadow: none; }

.btn-unstyled {
  background: transparent; }

.navbar-brand {
  padding: 10px 0;
  height: 80px; }

.logo {
  max-width: 100%;
  max-height: 100%; }

.prototype-warning {
  background-color: #2980B9;
  color: #fff;
  padding: 5px;
  text-align: center; }
  .prototype-warning a {
    color: #2C3E50;
    font-weight: 800; }

input[type=text].form-control {
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.15) !important;
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.15) !important;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 10px 15px; }

#content {
  min-height: 400px;
  margin-top: 30px; }

.prototype-warning {
  background-color: #3498db;
  color: #fff;
  padding: 5px;
  text-align: center; }
  .prototype-warning a {
    color: #2c3e50;
    font-weight: 800; }
  .prototype-warning .prototype-warning-dismiss {
    position: absolute;
    top: 0;
    right: 0;
    background: transparent;
    border: 0;
    padding: 6px 10px; }

@media (max-width: 768px) {
  .form-control:focus {
    border: none; }
  .navbar-brand {
    padding: 10px 15px; } }

.white-box {
  background: #fff;
  padding: 15px;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 10px; }

.media-left img {
  max-width: 100px; }

.media-body h3, .media-body h1, .media-body h2 {
  margin-top: 0; }

.media-body h1 {
  font-weight: normal; }

.license {
  font-size: 1rem;
  color: #16A085; }

.nav > li > a:focus, .nav > li > a:hover {
  background: transparent; }

a.badge {
  background: #DADDDE;
  margin-bottom: 5px;
  color: #000;
  font-weight: normal;
  border-radius: 5px;
  padding: 7px;
  margin-right: 5px; }
  a.badge:hover, a.badge:focus {
    background: #2980B9; }

.dont-break-out, .white-box a {
  /* These are technically the same, but use both */
  overflow-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  /* This is the dangerous one in WebKit, as it breaks things wherever */
  word-break: break-all;
  /* Instead use this non-standard one: */
  word-break: break-word;
  /* Adds a hyphen where the word breaks, if supported (No Blink) */
  -ms-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto; }

.btn.active, .btn:active {
  -webkit-box-shadow: none;
          box-shadow: none; }

.updated-date {
  font-size: 0.85rem;
  color: #888;
  margin-top: 5px; }

.nav > li > a:hover, .nav > li > a:focus {
  color: #888; }

.field-error {
  color: red; }

.form-error {
  border: 1px solid red; }

.success-message {
  color: #fff;
  background: #16A085;
  border-radius: 5px;
  padding: 12px;
  margin-bottom: 10px; }

@-webkit-keyframes hideMessage {
  0% {
    opacity: 1; }
  90% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes hideMessage {
  0% {
    opacity: 1; }
  90% {
    opacity: 1; }
  100% {
    opacity: 0; } }



