.login__row {
  margin-bottom: 1em; }

.login__providers {
  list-style: none;
  padding: 0; }

.login__provider {
  display: block;
  height: 3em; }

.login__logo {
  height: 3em;
  width: 3em;
  padding: 0.5em;
  display: inline-block;
  vertical-align: middle; }
  .login__logo--email {
    padding: 0.5em 0.7em; }

.login__email-icon {
  color: #c0392b;
  font-size: 1.7em; }

.login__form {
  margin-top: 1em; }

.login__input-group {
  margin: 0.5em 0; }



