.projects-viewer .projects {
  min-height: 400px; }

.project-status {
  display: inline-block;
  border-radius: 3px;
  line-height: 10px;
  font-size: 11px;
  padding: 3px; }
  .project-status.open {
    border: 1px solid #16A085;
    color: #16A085; }
  .project-status.closed {
    border: 1px solid #888;
    color: #888; }



