.data-preview-text-box {
  font-family: Courier;
  margin: 15px auto; }
  .data-preview-text-box pre {
    background: #fff;
    padding: 15px;
    white-space: pre-wrap;
    /* css-3 */
    white-space: -moz-pre-wrap;
    /* Mozilla, since 1999 */
    white-space: -pre-wrap;
    /* Opera 4-6 */
    white-space: -o-pre-wrap;
    /* Opera 7 */
    word-wrap: break-word;
    /* Internet Explorer 5.5+ */ }



