.record-details__sidebar {
  margin-top: 47px; }
  .record-details__sidebar > div {
    margin-bottom: 10px; }

.overview-toggle {
  margin: 10px auto 0 auto;
  display: block;
  background-color: #ecf0f1;
  border-radius: 50%;
  padding: 5px 8px; }

.dataset-discussion {
  padding-top: 20px;
  padding-bottom: 20px; }

.section-heading {
  font-weight: 400;
  color: #888; }

.publisher {
  color: #888;
  font-weight: bold; }

.preview-toggle {
  float: right;
  background: transparent;
  border: none;
  padding: 0;
  color: #2980B9; }
  .preview-toggle:hover {
    background: transparent; }



