.appContainer__nav {
  background: #ecf0f1; }

.navbar-header {
  background: transparent; }

.navbar-nav {
  float: right; }
  .navbar-nav a {
    color: #000000; }

.search-heading {
  font-weight: normal;
  margin-bottom: 30px;
  margin-top: 10px;
  font-size: 2rem; }

.mobile-nav .navbar-brand {
  height: auto; }

.mobile-nav img {
  height: 40px; }

.mobile-nav .navbar-toggle {
  background: #2980B9;
  color: #fff; }
  .mobile-nav .navbar-toggle:hover {
    background: #409ad5; }

.mobile-nav .navbar-collapse {
  width: 100%;
  margin-bottom: 5px; }
  .mobile-nav .navbar-collapse li {
    margin-bottom: 1px;
    background: #fff;
    border-radius: 3px; }

@media (min-width: 768px) {
  .decorator {
    padding: 15px 0; } }

.navbar-account.navbar-nav > li > a {
  text-decoration: underline;
  padding-left: 5px;
  padding-right: 5px; }

.appContainer__suggestion {
  padding: 15px;
  color: #fff;
  background: #2980B9; }
  .appContainer__suggestion a {
    color: #fff;
    text-decoration: underline; }

.footer {
  background-color: #2C3E50;
  color: #fff;
  padding: 50px 0; }
  .footer a {
    color: #fff;
    text-decoration: underline;
    font-size: 0.9rem; }
  .footer .nav > li > a {
    padding: 10px 0px; }
    .footer .nav > li > a:hover {
      background: transparent; }
  .footer .nav-title {
    text-transform: uppercase; }
  .footer .copyright {
    text-align: right; }
    .footer .copyright img {
      height: 40px; }



